// font-family: 'Anton', sans-serif;
// font-family: 'Noto Sans Display', sans-serif;

header {
  .navbar {
    .navbar-brand {
      font-family: 'Anton', sans-serif;
      font-weight: 400;
      font-size: 45px;
      color: #DC4731;
      text-shadow: 0 0 4px  #DC4731;
      padding: 0 25px 0 0;
    }
    .navbar-toggle {
      background-color: #DC4731;
    }
    .menubar {
      .navlink {
        font-family: 'Noto Sans Display', sans-serif;
        color: #FFF3D9;
        text-shadow: 0 0 2px  #FFF3D9;
        font-weight: 500;
        font-size: 16px;
        padding: 0 40px 0 0;
        &:hover {
          color: #DC4731;
          text-decoration: none;
        }
    }
    
    
    .user-menu {
      .username, .username-span {
        font-family: 'Noto Sans Display', sans-serif;
        color: #DC4731;
        text-shadow: 0 0 2px  #DC4731;
        font-style: italic;
        font-weight: 600;
        font-size: 20px;
      }
      .username-span:hover {
            color: #FFF3D9;
        }
      .username {
        font-family: 'Noto Sans Display', sans-serif;
        color: #DC4731;
        font-style: italic;
        font-weight: 600;
        font-size: 20px;

      }
      .user-btn {
        padding: 10px 10px;
        font-size: 20px;
        padding-bottom: 0 ;
        
      }
      .logout-btn {
        font-family: 'Noto Sans Display', sans-serif;
        font-weight: 400;
        background-color: #3B0918;
        border-color: #FFF3D9;
  
        border-radius: 3px;
        width: 80px;
        height: 35px;
        color: #FFF3D9;
        margin-left: 20px;
        margin-top: 8px;
        @media screen and (max-width: 990px) {
          margin-left: 0;
          margin-top: 20px;
        }
        &:hover {
            background-color: #FFF3D9;
            cursor: pointer;
            color: #3B0918;
        }
      }
    }

    .login {
      .login-btn {
        font-family: 'Noto Sans Display', sans-serif;
        font-weight: 400;
        background-color: #3B0918;
        border-color: #FFF3D9;
  
        border-radius: 3px;
        width: 80px;
        height: 35px;
        color: #FFF3D9;
        margin-left: 30px;
        margin-top: 8px;
        @media screen and (max-width: 990px) {
          margin-left: 0;
          margin-top: 20px;
        }
        &:hover {
            background-color: #FFF3D9;
            cursor: pointer;
            color: #3B0918;
        }
    }
  }
}
}
}
