.truncate {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-card-link:hover {
  cursor: pointer;
}


.card {
  background-color: #FFF3D9;
  border-radius: 20px;
  box-shadow: 5px 7px 9px #FFF3D9;
  padding: 0 ;

  @media screen and (max-width: 990px) {
    margin-bottom: 30px;
    
  }
}